.screen {
  min-height: calc(100vh - 200px);
  padding-bottom: 24px;
}

.inputGroup {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.inputLabel,
.textareaLabel {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.labelText {
  font-size: 14px;
  font-weight: 500;
  color: #374151;
}

.helperText {
  font-size: 13px;
  color: #6b7280;
  margin-top: -4px;
}

.input {
  width: 100%;
  font-size: 16px; /* Prevents iOS zoom */
  min-height: 44px;
  padding: 12px 14px;
  border: 1px solid #d1d5db;
  border-radius: 8px;
  background: #ffffff;
  font-family: inherit;
}

.input:focus {
  outline: none;
  border-color: #3b82f6;
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);
}

.input::placeholder {
  color: #9ca3af;
  font-style: italic;
}

.storySection {
  background: #ffffff;
  border-radius: 12px;
  padding: 16px;
  margin-top: 16px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.06);
}

.textarea {
  width: 100%;
  font-size: 16px; /* Prevents iOS zoom */
  padding: 12px 14px;
  border: 1px solid #d1d5db;
  border-radius: 8px;
  background: #ffffff;
  font-family: inherit;
  line-height: 1.6;
  resize: vertical;
  min-height: 120px;
}

.textarea:focus {
  outline: none;
  border-color: #3b82f6;
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);
}

.textarea::placeholder {
  color: #9ca3af;
  font-style: italic;
}

.aiSection {
  margin-top: 16px;
  display: flex;
  justify-content: center;
}

.aiButton {
  padding: 10px 20px;
  font-size: 15px;
  font-weight: 500;
  color: #6b7280;
  background: #f3f4f6;
  border: 1px solid #d1d5db;
  border-radius: 8px;
  cursor: not-allowed;
  opacity: 0.6;
}

/* Tablet and larger */
@media (min-width: 641px) {
  .inputGroup {
    flex-direction: row;
  }

  .inputLabel {
    flex: 1;
  }

  .storySection {
    padding: 20px;
  }

  .textarea {
    min-height: 200px;
  }
}

/* Print - show content, hide AI button */
@media print {
  .screen {
    padding: 0;
  }

  .aiSection {
    display: none;
  }

  .storySection {
    box-shadow: none;
    padding: 0;
    margin: 12pt 0;
  }

  .labelText {
    font-weight: 600;
    margin-bottom: 4pt;
  }

  .input,
  .textarea {
    border: none;
    padding: 0;
    background: transparent;
  }
}
