.container {
  min-height: 100vh;
  background: #f5f6f8;
}

.content {
  max-width: 700px;
  margin: 0 auto;
  padding: 0 16px;
  padding-bottom: 80px; /* Clearance for sticky footer on mobile */
}

.placeholder {
  background: #ffffff;
  border-radius: 12px;
  padding: 40px 24px;
  margin: 20px 0;
  text-align: center;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.06);
  min-height: 300px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.placeholder h2 {
  font-size: 22px;
  font-weight: 600;
  color: #111827;
  margin: 0 0 8px 0;
}

.placeholder p {
  font-size: 15px;
  color: #6b7280;
  margin: 0;
  max-width: 400px;
}

.loading {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  font-size: 16px;
  color: #6b7280;
}

/* Tablet and larger */
@media (min-width: 641px) {
  .content {
    padding: 0 24px;
    padding-bottom: 24px; /* No sticky footer on desktop */
  }

  .placeholder {
    padding: 60px 40px;
  }

  .placeholder h2 {
    font-size: 26px;
  }
}

/* Print - hide placeholders */
@media print {
  .container {
    background: #ffffff;
  }

  .content {
    max-width: none;
    padding: 0;
  }

  .placeholder {
    display: none;
  }
}
