.header {
  margin-bottom: 24px;
}

.title {
  font-size: 22px;
  font-weight: 600;
  color: #111827;
  margin: 0 0 8px 0;
  line-height: 1.3;
}

.subtitle {
  font-size: 14px;
  color: #6b7280;
  margin: 0;
  line-height: 1.5;
}

/* Tablet and larger */
@media (min-width: 641px) {
  .header {
    margin-bottom: 32px;
  }

  .title {
    font-size: 26px;
  }

  .subtitle {
    font-size: 15px;
  }
}

/* Print - simplify header */
@media print {
  .title {
    font-size: 18pt;
    margin-bottom: 4pt;
  }

  .subtitle {
    font-size: 10pt;
    margin-bottom: 12pt;
  }
}
