.screen {
  min-height: calc(100vh - 200px);
  padding-bottom: 24px;
}

.list {
  margin-bottom: 20px;
}

.addButton {
  width: 100%;
  min-height: 48px;
  font-size: 15px;
  font-weight: 500;
  color: #374151;
  background: #ffffff;
  border: 2px dashed #d1d5db;
  border-radius: 12px;
  cursor: pointer;
  transition: all 0.15s ease;
  touch-action: manipulation;
}

.addButton:hover {
  background: #f9fafb;
  border-color: #9ca3af;
  color: #111827;
}

.addButton:active {
  background: #f3f4f6;
  transform: scale(0.98);
}

/* Tablet and larger */
@media (min-width: 641px) {
  .addButton {
    min-height: 44px;
  }
}

/* Print - hide add button */
@media print {
  .screen {
    padding: 0;
  }

  .addButton {
    display: none;
  }
}
