.header {
  background: #ffffff;
  border-bottom: 1px solid #e5e7eb;
  padding: 16px;
  margin-bottom: 16px;
}

.textRow {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 12px;
}

.screenName {
  font-size: 18px;
  font-weight: 600;
  color: #111827;
}

.screenCounter {
  font-size: 14px;
  color: #6b7280;
}

/* Progress bar (tablet/desktop) */
.progressBarContainer {
  width: 100%;
  height: 6px;
  background: #e5e7eb;
  border-radius: 3px;
  overflow: hidden;
}

.progressBarFill {
  height: 100%;
  background: linear-gradient(90deg, #3b82f6, #60a5fa);
  transition: width 0.3s ease;
  border-radius: 3px;
}

/* Progress dots (mobile) */
.progressDots {
  display: flex;
  gap: 8px;
  justify-content: center;
  align-items: center;
}

.dot {
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: #d1d5db;
  transition: all 0.3s ease;
}

.dotActive {
  background: #3b82f6;
  width: 12px;
  height: 12px;
}

.dotCompleted {
  background: #10b981;
}

/* Tablet and larger */
@media (min-width: 641px) {
  .header {
    padding: 20px 24px;
  }

  .screenName {
    font-size: 22px;
  }

  .screenCounter {
    font-size: 15px;
  }

  .progressBarContainer {
    height: 8px;
  }
}

/* Print - hide navigation header */
@media print {
  .header {
    display: none;
  }
}
