.container {
  font-family: -apple-system, BlinkMacSystemFont, 'SF Pro Text', Roboto, sans-serif;
  font-size: 11pt;
  line-height: 1.5;
  color: #1a1a1a;
}

.title {
  font-size: 18pt;
  font-weight: 700;
  margin: 0 0 6pt 0;
  color: #111;
  border-bottom: 2px solid #333;
  padding-bottom: 8pt;
}

/* Patient header - prominent display */
.patientHeader {
  background: #f5f5f5;
  padding: 10pt 12pt;
  margin-bottom: 16pt;
  border-radius: 4pt;
}

.patientName {
  font-size: 14pt;
  font-weight: 700;
  margin: 0 0 4pt 0;
  color: #111;
}

.patientDob {
  font-size: 11pt;
  margin: 0;
  color: #444;
}

.section {
  margin-bottom: 14pt;
  padding-bottom: 10pt;
  border-bottom: 1px solid #ddd;
}

.section:last-child {
  border-bottom: none;
}

.sectionTitle {
  font-size: 12pt;
  font-weight: 700;
  margin: 0 0 6pt 0;
  color: #333;
  text-transform: uppercase;
  letter-spacing: 0.3pt;
}

.sectionList {
  margin: 0;
  padding-left: 1.2em;
  list-style-type: disc;
}

.listItem {
  margin-bottom: 4pt;
  line-height: 1.4;
}

.listItem strong {
  color: #111;
}

.sectionContent {
  margin: 0;
  white-space: pre-wrap;
  line-height: 1.5;
}

/* Allergies - critical safety highlight */
.allergiesSection {
  background: #fff5f5;
  border: 2px solid #dc2626;
  border-radius: 4pt;
  padding: 8pt 12pt;
  margin-bottom: 14pt;
}

.allergiesSection .sectionTitle {
  color: #dc2626;
}

.allergiesSection .sectionList {
  margin-bottom: 0;
}

/* Medications - slight highlight */
.medicationsSection {
  background: #f0f9ff;
  border: 1px solid #0284c7;
  border-radius: 4pt;
  padding: 8pt 12pt;
  margin-bottom: 14pt;
}

.medicationsSection .sectionTitle {
  color: #0369a1;
}

