.section {
  background: #ffffff;
  border-radius: 12px;
  margin-bottom: 16px;
  overflow: hidden;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.06);
}

.header {
  width: 100%;
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 16px;
  background: #ffffff;
  border: none;
  cursor: pointer;
  font-size: 16px;
  font-weight: 600;
  color: #111827;
  text-align: left;
  transition: background 0.15s ease;
}

.header:hover {
  background: #f9fafb;
}

.header:active {
  background: #f3f4f6;
}

.icon {
  font-size: 12px;
  color: #6b7280;
  flex-shrink: 0;
  transition: transform 0.2s ease;
}

.title {
  flex: 1;
}

.badge {
  font-size: 14px;
  font-weight: 500;
  color: #6b7280;
  background: #f3f4f6;
  padding: 2px 8px;
  border-radius: 12px;
}

.action {
  flex-shrink: 0;
}

.content {
  padding: 0 16px 16px 16px;
  animation: slideDown 0.2s ease;
}

@keyframes slideDown {
  from {
    opacity: 0;
    transform: translateY(-8px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Tablet and larger */
@media (min-width: 641px) {
  .header {
    padding: 20px;
    font-size: 17px;
  }

  .content {
    padding: 0 20px 20px 20px;
  }
}

/* Print - always show content */
@media print {
  .header {
    padding: 8pt 0;
    background: transparent;
    border-bottom: 1pt solid #e5e7eb;
    cursor: default;
  }

  .icon,
  .action {
    display: none;
  }

  .content {
    padding: 8pt 0;
  }
}
