.container {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #f5f6f8;
  padding: 20px;
}

.card {
  background: #ffffff;
  border-radius: 16px;
  padding: 32px 28px;
  width: 100%;
  max-width: 400px;
  box-shadow: 0 4px 24px rgba(0, 0, 0, 0.08);
}

.logo {
  text-align: center;
  margin-bottom: 24px;
}

.logoImage {
  width: 80px;
  height: 80px;
  border-radius: 16px;
}

.form h2 {
  font-size: 20px;
  font-weight: 600;
  color: #111827;
  margin: 0 0 20px 0;
  text-align: center;
}

.subtitle {
  font-size: 14px;
  color: #6b7280;
  text-align: center;
  margin: -12px 0 16px 0;
}

.inputGroup {
  margin-bottom: 16px;
}

.inputGroup label {
  display: block;
  font-size: 14px;
  font-weight: 500;
  color: #374151;
  margin-bottom: 6px;
}

.inputGroup input {
  width: 100%;
  padding: 12px 14px;
  font-size: 16px;
  border: 1px solid #d1d5db;
  border-radius: 8px;
  background: #ffffff;
  transition: border-color 0.15s, box-shadow 0.15s;
  box-sizing: border-box;
}

.inputGroup input:focus {
  outline: none;
  border-color: #a6370b;
  box-shadow: 0 0 0 3px rgba(166, 55, 11, 0.1);
}

.inputGroup input:disabled {
  background: #f3f4f6;
  cursor: not-allowed;
}

.error {
  background: #fef2f2;
  color: #dc2626;
  padding: 12px 14px;
  border-radius: 8px;
  font-size: 14px;
  margin-bottom: 16px;
}

.success {
  background: #f0fdf4;
  color: #16a34a;
  padding: 12px 14px;
  border-radius: 8px;
  font-size: 14px;
  margin-bottom: 16px;
  text-align: center;
}

.primaryButton {
  width: 100%;
  padding: 14px;
  font-size: 16px;
  font-weight: 600;
  color: #ffffff;
  background: #a6370b;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.15s;
}

.primaryButton:hover:not(:disabled) {
  background: #8a2e09;
}

.primaryButton:disabled {
  background: #d1d5db;
  cursor: not-allowed;
}

.linkButton {
  display: block;
  width: 100%;
  padding: 8px;
  font-size: 14px;
  color: #6b7280;
  background: none;
  border: none;
  cursor: pointer;
  text-align: center;
  margin-top: 8px;
}

.linkButton:hover {
  color: #a6370b;
  text-decoration: underline;
}

.divider {
  display: flex;
  align-items: center;
  margin: 20px 0;
}

.divider::before,
.divider::after {
  content: '';
  flex: 1;
  height: 1px;
  background: #e5e7eb;
}

.divider span {
  padding: 0 12px;
  font-size: 13px;
  color: #9ca3af;
}

.googleButton {
  width: 100%;
  padding: 12px;
  font-size: 15px;
  font-weight: 500;
  color: #374151;
  background: #ffffff;
  border: 1px solid #d1d5db;
  border-radius: 8px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  transition: background-color 0.15s, border-color 0.15s;
}

.googleButton:hover:not(:disabled) {
  background: #f9fafb;
  border-color: #9ca3af;
}

.googleButton:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.googleIcon {
  flex-shrink: 0;
}

.guestButton {
  width: 100%;
  padding: 12px;
  font-size: 15px;
  font-weight: 500;
  color: #6b7280;
  background: #f3f4f6;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  margin-top: 10px;
  transition: background-color 0.15s;
}

.guestButton:hover:not(:disabled) {
  background: #e5e7eb;
}

.guestButton:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.switchMode {
  font-size: 14px;
  color: #6b7280;
  text-align: center;
  margin: 20px 0 0 0;
}

.switchMode button {
  background: none;
  border: none;
  color: #a6370b;
  font-weight: 500;
  cursor: pointer;
  padding: 0;
}

.switchMode button:hover {
  text-decoration: underline;
}

.guestNote {
  font-size: 12px;
  color: #9ca3af;
  text-align: center;
  margin: 24px 0 0 0;
  line-height: 1.5;
  padding-top: 16px;
  border-top: 1px solid #e5e7eb;
}

.guestNote strong {
  color: #6b7280;
}

/* Mobile adjustments */
@media (max-width: 480px) {
  .container {
    padding: 16px;
    align-items: flex-start;
    padding-top: 40px;
  }

  .card {
    padding: 24px 20px;
  }

  .logo h1 {
    font-size: 28px;
  }
}
