.screen {
  min-height: calc(100vh - 200px);
  padding-bottom: 24px;
}

.sectionContent {
  padding-top: 8px;
}

.itemCard {
  background: #f9fafb;
  border: 1px solid #e5e7eb;
  border-radius: 8px;
  padding: 14px;
  margin-bottom: 12px;
}

.itemHeader {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 12px;
}

.itemNumber {
  font-size: 13px;
  font-weight: 600;
  color: #6b7280;
}

.deleteButton {
  font-size: 12px;
  font-weight: 500;
  color: #dc2626;
  background: transparent;
  border: 1px solid #fca5a5;
  border-radius: 5px;
  padding: 3px 10px;
  cursor: pointer;
  transition: all 0.15s ease;
}

.deleteButton:hover {
  background: #fef2f2;
  border-color: #dc2626;
}

.deleteButton:active {
  background: #fee2e2;
}

.field {
  display: flex;
  flex-direction: column;
  gap: 6px;
  margin-bottom: 12px;
}

.field:last-child {
  margin-bottom: 0;
}

.label {
  font-size: 13px;
  font-weight: 500;
  color: #374151;
}

.input,
.textarea {
  width: 100%;
  font-size: 16px; /* Prevents iOS zoom */
  min-height: 44px;
  padding: 10px 12px;
  border: 1px solid #d1d5db;
  border-radius: 6px;
  background: #ffffff;
  font-family: inherit;
}

.textarea {
  min-height: 60px;
  resize: vertical;
  line-height: 1.5;
}

.input:focus,
.textarea:focus {
  outline: none;
  border-color: #3b82f6;
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);
}

.input::placeholder,
.textarea::placeholder {
  color: #9ca3af;
  font-style: italic;
}

.addButton {
  width: 100%;
  min-height: 44px;
  font-size: 14px;
  font-weight: 500;
  color: #374151;
  background: #ffffff;
  border: 2px dashed #d1d5db;
  border-radius: 8px;
  cursor: pointer;
  margin-top: 8px;
  transition: all 0.15s ease;
  touch-action: manipulation;
}

.addButton:hover {
  background: #f9fafb;
  border-color: #9ca3af;
  color: #111827;
}

.addButton:active {
  background: #f3f4f6;
  transform: scale(0.98);
}

/* Print styles */
@media print {
  .screen {
    padding: 0;
  }

  .itemCard {
    border: 1px solid #e5e7eb;
    margin-bottom: 8pt;
    page-break-inside: avoid;
  }

  .itemHeader {
    margin-bottom: 4pt;
  }

  .deleteButton,
  .addButton {
    display: none;
  }

  .field {
    margin-bottom: 6pt;
  }

  .label {
    font-weight: 600;
  }

  .input,
  .textarea {
    border: none;
    padding: 2pt 0;
    min-height: auto;
  }
}
