.footer {
  margin-top: 48px;
  padding: 24px 16px;
  border-top: 1px solid #e0e0e0;
  background-color: #f9f9f9;
}

.disclaimers {
  margin-bottom: 16px;
}

.disclaimer {
  font-size: 12px;
  line-height: 1.5;
  color: #666;
  margin-bottom: 12px;
}

.disclaimer:last-child {
  margin-bottom: 0;
}

.disclaimer strong {
  color: #333;
  font-weight: 600;
}

.contact {
  text-align: center;
  font-size: 12px;
  color: #666;
  margin-bottom: 12px;
}

.contact p {
  margin: 0;
}

.contact a {
  color: #0369a1;
  text-decoration: none;
}

.contact a:hover {
  text-decoration: underline;
}

.copyright {
  text-align: center;
  font-size: 12px;
  color: #999;
  padding-top: 12px;
  border-top: 1px solid #e0e0e0;
}

.copyright p {
  margin: 0;
}

/* Tablet and Desktop */
@media (min-width: 768px) {
  .footer {
    padding: 32px 24px;
  }

  .disclaimer {
    font-size: 13px;
  }

  .copyright {
    font-size: 13px;
  }
}
